<template>
    <div class="iframebox">
        <iframe :src="iframeUrl" frameborder="0" :style="{height:iframHeight}">
            
        </iframe>
    </div>
</template>
<script>
export default {
    name:'iframes_mine',
    data(){
        return{
            iframHeight:'',
            iframeUrl: this.$route.query.iframeUrl,
            pageData: ''
        }
    },
    mounted(){
        var _this = this;
        this.iframHeight = window.innerHeight+'px';
        console.log(this.$route.query.iframeUrl);
        // this.requests({cancleHost:true,reqUrl:this.$route.query.iframeUrl}).then(function(res){
        //     console.log(res);
        //     _this.pageData = res.data;
        // })
    },
    methods:{}
}
</script>



